import React, { useEffect, useState } from "react";
import axios from "axios";
import swal from "sweetalert";
import { API, config } from "../../config";
import Logout from "../../store/global/logout";
import FormLoader from "../components/FormLoader/FormLoader";
import FormHeader from "../components/FormHeader/FormHeader";
import FormInput from "../components/FormInput/FormInput";
import FormSubmit from "../components/FormSubmit/FormSubmit";
import classNames from "./Profile.module.css";
import FormUpload from "../components/FormUpload/FormUpload";
import checkLoggedIn from "../../store/global/checkLoggedIn";
import { NavLink } from "react-router-dom";

const Profile = ({ history }) => {
  const [verifyEmail, setVerifyEmail] = useState(false);
  const [loading, setLoading] = useState(false);
  const [loadingPass, setLoadingPass] = useState(false);
  const [loadingToken, setLoadingToken] = useState(false);
  const [loadingDelete, setLoadingDelete] = useState(false);

  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [namePlaceholder, setNamePlaceholder] = useState("");
  const [emailPlaceholder, setEmailPlaceholder] = useState("");
  const [img, setImg] = useState("");
  const [image, setImgView] = useState("");

  const [token, setToken] = useState("");

  const [password, setPassword] = useState("");
  const [currentPassword, setCurrentPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");

  const setInitialUser = () => {
    if (!localStorage || !localStorage.getItem("user")) return;
    const user = JSON.parse(localStorage.getItem("user"));
    setNamePlaceholder(user.name);
    setImgView(user.image);
    setEmailPlaceholder(user.email);
  };

  useEffect(() => {
    checkLoggedIn(history)().then((res) => {
      if (!res) history.push("/");
    });
    setInitialUser();
  }, [history]);

  const setImage = (v) => {
    setImg(v);
    const reader = new FileReader();
    reader.onload = () => {
      if (reader.readyState === 2) {
        setImgView(reader.result);
      }
    };
    reader.readAsDataURL(v);
  };

  const updateUser = () => {
    setLoading(true);
    const data = new FormData();
    if (name.length > 0) {
      data.append("name", name);
    }
    if (email.length > 0) {
      data.append("email", email);
    }
    if (img.type) {
      data.append("image", img);
    }
    axios
      .patch(`${API}/update-user`, data, config())
      .then((response) => {
        setLoading(false);

        document.getElementById("name").value = "";
        document.getElementById("email").value = "";
        document.getElementById("image").value = "";

        setInitialUser();

        if (response.data.verify) {
          swal("Success!", response.data.message, "success", {
            button: "Okay",
          }).then(() => {
            setVerifyEmail(true);
          });
        } else {
          localStorage.setItem("user", JSON.stringify(response.data.data.user));
          swal("Success!", "Profile updated successfully!", "success", {
            button: "Okay",
          });
        }
      })
      .catch((err) => {
        setLoading(false);
        swal("Error!", `${err.response.data.message}`, "error", {
          button: "Okay",
        });
      });
  };

  const updatePassword = () => {
    setLoadingPass(true);
    const data = {
      password,
      confirmPassword,
      currentPassword,
    };
    axios
      .patch(`${API}/update-password`, data, config())
      .then((response) => {
        setLoadingPass(false);
        localStorage.setItem("authToken", JSON.stringify(response.data.token));

        document.getElementById("currentPassword").value = "";
        document.getElementById("newPassword").value = "";
        document.getElementById("confirmPassword").value = "";

        setInitialUser();
        swal("Success!", "Password updated successfully!", "success", {
          button: "Okay",
        });
      })
      .catch((err) => {
        setLoadingPass(false);
        swal("Error!", `${err.response.data.message}`, "error", {
          button: "Okay",
        });
      });
  };

  const logout = (e) => {
    e.preventDefault();
    Logout(history);
  };

  const verifyEmailWithToken = () => {
    setLoadingToken(true);
    axios
      .get(`${API}/verify-email/${token}`, config())
      .then((response) => {
        setLoadingToken(false);
        localStorage.setItem("user", JSON.stringify(response.data.data.user));
        setInitialUser();
        swal(
          "Verification success!",
          "Your email has been updated successfully!",
          "success",
          {
            button: "Okay",
          }
        ).then(() => {
          setVerifyEmail(false);
          document.getElementById("name").value = "";
        });
      })
      .catch((err) => {
        setLoadingToken(false);
        swal("Error!", `${err.response.data.message}`, "error", {
          button: "Okay",
        });
      });
  };

  const deleteUser = () => {
    setLoadingDelete(true);
    axios
      .delete(`${API}/delete-user`, config())
      .then((response) => {
        setLoadingDelete(false);
        localStorage.clear();
        swal(
          "Good bye!",
          "Your account has been deleted successfully!",
          "info",
          {
            button: "Bye",
          }
        ).then(() => {
          history.push("/");
        });
      })
      .catch((err) => {
        setLoadingDelete(false);
        swal("Error!", `${err.response.data.message}`, "error", {
          button: "Okay",
        });
      });
  };

  return (
    <div className={classNames.container}>
      <div className={classNames.profile_image}>
        <div className={classNames.image}>
          <img src={image} alt="Profile" />
        </div>
        <div className={classNames.name}>
          <h1>{namePlaceholder}</h1>
        </div>
        <div className={classNames.navigations}>
          <NavLink to="/app">Go to app</NavLink>
          <a href="void:javascript" onClick={logout}>
            Logout
          </a>
        </div>
      </div>
      <div className={classNames.profile_details}>
        <div className={classNames.update_profile}>
          <FormHeader title="Update Profile" />
          {verifyEmail ? (
            <>
              <FormInput
                i={{
                  id: "token",
                  type: "text",
                  placeholder: "Paste your token here",
                  label: "Paste token",
                  setFn: setToken,
                }}
              />
              {loadingToken ? (
                <FormLoader />
              ) : (
                <FormSubmit
                  text="Verify Email"
                  clickFn={verifyEmailWithToken}
                />
              )}
            </>
          ) : (
            <>
              <FormInput
                i={{
                  id: "name",
                  type: "text",
                  placeholder: `${namePlaceholder}`,
                  label: "Enter your new name",
                  setFn: setName,
                }}
              />
              <FormInput
                i={{
                  id: "email",
                  type: "email",
                  placeholder: `${emailPlaceholder}`,
                  label: "Enter your new email",
                  setFn: setEmail,
                }}
              />
              <FormUpload
                i={{
                  id: "image",
                  setFn: setImage,
                }}
              />
              {loading ? (
                <FormLoader />
              ) : (
                <FormSubmit text="Update Profile" clickFn={updateUser} />
              )}
            </>
          )}
        </div>
        <div className={classNames.update_password}>
          <FormHeader title="Update Password" />
          <FormInput
            i={{
              id: "currentPassword",
              type: "password",
              placeholder: "********",
              label: "Current Password",
              setFn: setCurrentPassword,
            }}
          />
          <FormInput
            i={{
              id: "newPassword",
              type: "password",
              placeholder: "********",
              label: "New Password",
              setFn: setPassword,
            }}
          />
          <FormInput
            i={{
              id: "confirmPassword",
              type: "password",
              placeholder: "********",
              label: "Confirm Password",
              setFn: setConfirmPassword,
            }}
          />
          {loadingPass ? (
            <FormLoader />
          ) : (
            <FormSubmit text="Update Password" clickFn={updatePassword} />
          )}
        </div>
        <div className={classNames.delete_user}>
          <FormHeader title="Danger Zone" />
          {loadingDelete ? (
            <FormLoader />
          ) : (
            <FormSubmit text="Delete account" clickFn={deleteUser} />
          )}
        </div>
      </div>
    </div>
  );
};

export default Profile;
